import { headerAPI } from "../api/api";
import { stopSubmit } from "redux-form";

const SET_USER_DATA = 'auth/SET_USER_DATA';
const TOGGLE_IS_FETCHING = "TOGGLE_IS_FETCHING";

let initialState = {
  userId: null,
  email: null,
  login: null,
  isAuth: false,
  isFetching: false
}

const authReducer = (state = initialState, action) => {
  switch(action.type){
    case SET_USER_DATA:
      return{
        ...state,
        ...action.payload
      }
    case TOGGLE_IS_FETCHING:
      return {
        ...state, 
        isFetching: action.isFetching
      }
    default:
      return state;
  }
}

export const setAuthUserData = (userId, email, login, isAuth)=>
({type: SET_USER_DATA, payload: {userId, email, login, isAuth}})
export const setIsFetching = (isFetching) => ({type: TOGGLE_IS_FETCHING, isFetching})

export const getHeader = () => async (dispatch)=> {
  dispatch(setIsFetching(true))
  let responseData = await headerAPI.getHeader()
    dispatch(setIsFetching(false))
    if(responseData.resultCode === 0){
      let {id, email, login} = responseData.data;
      dispatch(setAuthUserData(id, email, login, true)) 
    }
}
export const login = (email, password, rememberMe) => async (dispatch)=> {

  let responseData = await headerAPI.login(email, password, rememberMe);
    if(responseData.resultCode === 0){
      dispatch(getHeader())
    } else{
      let message = responseData.messages.length > 0 ?
      responseData.messages[0] : 'Не известная ошибка' 
      dispatch(stopSubmit('login', {_error: message})) 
    }
}
export const logout = () => async (dispatch)=> {
  let responseData = await headerAPI.logout();
    if(responseData.resultCode === 0){
      dispatch(setAuthUserData(null, null, null, false)) 
    }
}
export default authReducer;