import { profileAPI } from "../api/api";

const ADD_POST = 'ADD_POST';
const SET_USER_PROFILE = 'SET_USER_PROFILE';
const SET_STATUS = 'SET_STATUS';
const DELETE_POST = 'DELETE_POST';

let initialState = {
  postsData: [
    {id: 1, message: "Ваше сообщение!", like: "44"  }
  ],
  profile: null,
  status: ''
}

const profileReducer = (state = initialState, action) => {
  switch(action.type) {
    case ADD_POST:
      let newPost = {
        id: 5,
        message: action.value,
        like: 0
      };
      return {
        ...state,
        postsData: [...state.postsData, newPost]
      }
    case SET_USER_PROFILE :
      return {
        ...state,
        profile: action.profile,
      }
    case SET_STATUS :
      return {
        ...state,
        status: action.status,
      }
    case DELETE_POST :
      return {
        ...state,
        postsData: state.postsData.filter(p => p.id !== action.idPost)
      }
    default:
      return state;
  }
}

export const addPost = (value) => ({
  type: ADD_POST, value})

export const deletePost = (idPost) => ({
  type: DELETE_POST, idPost})

export const setUserProfile = (profile) => ({
    type: SET_USER_PROFILE,
    profile,
})
export const setStatus = (status) => ({
    type: SET_STATUS,
    status,
})

export const updateUserStatus = (status) => async (dispatch) => {
  let responseData = await profileAPI.updateStatus(status);
    if(responseData.resultCode === 0){
      dispatch(setStatus(status))}
}

export const getUserStatus = (userId) => async (dispatch) => {
  let responseData = await profileAPI.getStatus(userId);
    dispatch(setStatus(responseData))
}
export const getProfile = (userId) => async (dispatch) => {
  let responseData = await profileAPI.getProfile(userId);
    dispatch(setUserProfile(responseData))
}

export default profileReducer;