import React from 'react';
import logo from '../../assets/images/logo-js-2.png'
import s from './Header.module.css'
import { NavLink } from 'react-router-dom';

const Header = (props) => {
  return (
    <header className={s.header}>
      <img src={logo} alt=""/>

      <div className={s.login}>
        {props.isAuth
        ? <div><NavLink to={'/profile/' + props.userId}>{props.login} /</NavLink><button onClick={props.logout}>Выйти</button></div>
        :<NavLink to={'/login'}>Войти</NavLink>}
      </div>
    </header>
  )
};

export default Header;