import React, { Component } from 'react';
import './App.css';
import Navbar from './componenti/navbar/Navbar';
import { Route, withRouter, HashRouter } from 'react-router-dom';
import UsersContainer from './componenti/users/UsersContainer';
import HeaderContainer from './componenti/header/HeaderContainer';
import Login from './componenti/login/login';
import {initializeApp} from './redux/app-reducer';
import { connect, Provider } from 'react-redux';
import { compose } from 'redux';
import Preloader from './componenti/common/preloader/preoader';
import store from './redux/redux-store';
import { WithSuspense } from './hoc/withSuspense'

//import ProfileContainer from './componenti/profile/ProfileContainer';
//import DialogsContainer from './componenti/dialogs/DialogsContainer';
//import SettingsContainer from './componenti/settings/settingsContainer';
//import NewsContainer from './componenti/news/newsContainer';
//import MusicContainer from './componenti/music/musicContainer';

const DialogsContainer = React.lazy(() => import ('./componenti/dialogs/DialogsContainer'));
const ProfileContainer = React.lazy(() => import ('./componenti/profile/ProfileContainer'));
const SettingsContainer = React.lazy(() => import ('./componenti/settings/settingsContainer'));
const NewsContainer = React.lazy(() => import ('./componenti/news/newsContainer'));
const MusicContainer = React.lazy(() => import ('./componenti/music/musicContainer'));

class App extends Component{
  componentDidMount(){
    this.props.initializeApp()
  }
  render() {
    // if(!this.props.initialized){
    //   return <Preloader app={true}/>
    // }
  return (
      <div className="App">
        <HeaderContainer />
        <Navbar />
        <div className="App-wrapper__content">
          <Route path='/profile/:userId?' render={
            WithSuspense(ProfileContainer)
          }/>
          <Route path='/dialogs' render={
            WithSuspense(DialogsContainer)
          }/>
          <Route path='/users' render={ () => <UsersContainer/>} />
          <Route path='/login' render={ () => <Login/>} />
          <Route path='/news' render={
            WithSuspense(NewsContainer)
          }/>
          <Route path='/music' render={
            WithSuspense(MusicContainer)
          }/>
          <Route path='/settings' render={
            WithSuspense(SettingsContainer)
          }/>
        </div>
      </div>
  );
  }
}

const mapStateToProps = (state) => ({
  initialized: state.app.initialized
})

const AppContainer = compose(
  withRouter,
  connect(mapStateToProps, {initializeApp})
)(App);

const PahaJsApp = () => {
  return <HashRouter basename={process.env.PUBLIC_URL}>
    <Provider store={store}>
      <AppContainer/>
    </Provider>
  </HashRouter>
}

export default PahaJsApp;