import React from 'react';
import style from './login.module.css'
import { reduxForm } from 'redux-form';
import { Input, createField } from '../common/FormControls/FormsControls';
import { required, maxLengthCreator } from '../../utls/validators/validators';
import { connect } from 'react-redux';
import { login } from '../../redux/auth-reducer'
import { Redirect } from 'react-router-dom';
import st from '../common/FormControls/FormsControls.module.css'

const maxlength20 = maxLengthCreator(20);

const Login = (props) => {
  const onSubmit = (formData) => {
    props.login(formData.email, formData.password, formData.rememberMe)
  }
  if (props.isAuth) {
    return <Redirect to={"/profile"} />
  }
  return(
    <div className={style.login}>
      <h1>Войти</h1>
      <LoginReduxForm onSubmit={onSubmit}/>
    </div>
  )
}

const LoginForm = ({handleSubmit, error}) => {
  return(
     <form onSubmit={handleSubmit}>
       <div className={style.input_form}>
       {createField('Email', 'email', [required, maxlength20], Input)}
       </div>
       <div>
       {createField('Пароль', 'password', [required, maxlength20], Input, 'password')}
       </div>
       <div className={style.checkbox}>
       {createField(null, 'rememberMe', null, Input, 'checkbox', 'checked')}
       запомнить</div>
       {error && <div className={st.form_sumary_error}>
         {error}
       </div>}
       <div className={style.button}>
       <button>Войти</button>
       </div>
     </form>
  )
}

const mapStateToProps = (state) => ({
  isAuth: state.auth.isAuth
})

const LoginReduxForm = reduxForm ({
  form: 'login' 
})(LoginForm)

export default connect(mapStateToProps, {login})(Login);