import React from 'react';
import loading from '../../../assets/images/loading-4.gif';
import style from './preloader.module.css'


const Preloader = (props) => {
  return(
    <div>
    {props.app && <div className={style.gif_loading_all_app}>
      <div>
      <img alt="" className={style.gif_loading_center_app} src={loading}/>
      </div>
      Ожидайте... Идёт загрузка!
      </div> }
      {props.profile && <div className={style.gif_loading_all_profile}>
      <div>
      <img alt="" className={style.gif_loading_center_profile} src={loading}/>
      </div>
      Идёт загрузка...
      </div>}
      {props.user && <div className={style.gif_loading_all_user}>
      <div>
      <img alt="" className={style.gif_loading_center_user} src={loading}/>
      </div>Идёт загрузка...
      </div>}
      {props.lazyLoading && <div className={style.gif_loading_all_user}>
      <div>
      <img alt="" className={style.lazyLoading} src={loading}/>
      </div>Идёт загрузка...
      </div>}
    </div>
  )
}
export default Preloader;



