const ADD_MESSAGE = 'ADD_MESSAGE';

let initialState = {
  messagesData: [
    {id: 1, message: "Здарова" },
    {id: 2, message: "Оо привет" },
    {id: 3, message: "Че как ?" },
    {id: 4, message: "Настрой" },
    {id: 5, message: "Да все нормально!" },
    {id: 6, message: "Че как ?" },
    {id: 7, message: "Че как ?" },
  ],
  dialogsData: [
    { id: 1, name: 'Паша' },
    { id: 2, name: 'Гриша' },
    { id: 3, name: 'Олег' },
    { id: 4, name: 'Валера' },
    { id: 5, name: 'Артём' },
    { id: 6, name: 'Алина' },
  ]
}

const dialogsReducer = (state = initialState, action) => {
  switch(action.type) {
    case ADD_MESSAGE:
      let newMessage = {
        id: 8,
        message: action.value
      };
      return {
        ...state,
        messagesData: [...state.messagesData, newMessage]
      };
    default:
      return state;
  }
}

export const addMessage = (value) => ({
  type: ADD_MESSAGE, value
})

export default dialogsReducer;