import { usersAPI } from "../api/api";
import { updateObjectInArray } from "../utls/object-helpers";

const FOLLOW = 'FOLLOW';
const UNFOLLOW = 'UNFOLLOW';
const SET_USERS = 'SET_USERS';
const SET_CURRENT_PAGE = 'SET_CURRENT_PAGE';
const SET_TOTAL_USERS_COUNT = 'SET_TOTAL_USERS_COUNT';
const TOGGLE_IS_FETCHING = "TOGGLE_IS_FETCHING";
const TOGGLE_IS_FETCHING_PROGRESS = "TOGGLE_IS_FETCHING_PROGRESS";

let initialState = {
  users: [],
  pageSize: 10,
  totalUsersCount: 0,
  currentPage: 1,
  isFetching: false,
  followingInProgress: []
};

const usersReducer = (state = initialState, action) => {
  switch(action.type){
    case FOLLOW:
      return {
        ...state,
        users: updateObjectInArray(state.users, action.userId, 'id', {followed: true})
      }
    case UNFOLLOW:
      return {
        ...state,
        users: updateObjectInArray(state.users, action.userId, 'id', {followed: false})
      }
    case SET_USERS:
      return {
        ...state, 
        users: action.users
      }
    case SET_CURRENT_PAGE:
      return {
        ...state, 
        currentPage: action.currentPage
      }
    case SET_TOTAL_USERS_COUNT:
      return {
        ...state, 
        totalUsersCount: action.totalCount
      }
    case TOGGLE_IS_FETCHING:
      return {
        ...state, 
        isFetching: action.isFetching
      }
    case TOGGLE_IS_FETCHING_PROGRESS:
      return {
        ...state, 
        followingInProgress: action.followingInProgress 
        ? [...state.followingInProgress, action.userId]
        : state.followingInProgress.filter(id => id !== action.userId)
      }
  default:
    return state
  }
}
export const followSuccess = (userId) => ({type: FOLLOW, userId})
export const unfollowSuccess = (userId) => ({type: UNFOLLOW, userId})
export const setUsers = (users) => ({type: SET_USERS, users})
export const setCurrentPage = (currentPage) => ({type: SET_CURRENT_PAGE, currentPage})
export const setTotalUsersCount = (totalCount) => ({type: SET_TOTAL_USERS_COUNT, totalCount})
export const setIsFetching = (isFetching) => ({type: TOGGLE_IS_FETCHING, isFetching})
export const setFollowingInProgress = (followingInProgress, userId) => ({type: TOGGLE_IS_FETCHING_PROGRESS, followingInProgress, userId})

export const getUsersT = (page, pageSize) => async (dispatch) => {
  dispatch(setCurrentPage(page))
  dispatch(setIsFetching(true))
  let responseData = await usersAPI.getUsers(page, pageSize);
    dispatch(setIsFetching(false))
    dispatch(setUsers(responseData.items))
    dispatch(setTotalUsersCount(responseData.totalCount))
}

const followUnfollowFlow = async (dispatch, id, apiMethod, actionCreator) => {
  dispatch(setFollowingInProgress(true, id))
  let responseData = await apiMethod(id);
    if(responseData.resultCode === 0){
      dispatch(actionCreator(id))
      }
    dispatch(setFollowingInProgress(false, id))          
}

export const follow = (id) => async (dispatch) => {
  followUnfollowFlow(dispatch, id, usersAPI.postFollow.bind(id), followSuccess)
}
export const unfollow = (id) => async (dispatch) => {
  followUnfollowFlow(dispatch, id, usersAPI.deleteUnFollow.bind(id), unfollowSuccess)
}

export default usersReducer;

