import React from 'react'
import style from './FormsControls.module.css'
import { Field } from 'redux-form';


export const Textarea = ({input, meta, ...props}) => {

  const hasError = meta.touched && meta.error;

  return(
    <div className={style.form_control + " " + (hasError ? style.error : '')}>
      <div>
        <textarea {...input} {...props}/>
      </div>
      <div>
  {hasError && <span>{meta.error}</span>}
      </div>
    </div>
  )
}
export const Input = ({input, meta, ...props}) => {
  const hasError = meta.touched && meta.error;
  return(
    <div className={style.form_control_input + " " + (hasError ? style.error : '')}>
      <div>
        <input {...input} {...props}/>
      </div>
      <div>
  {hasError && <span>{meta.error}</span>}
      </div>
    </div>
  )
}
export const createField = (placeholder, name, validate, component, type, checked) => {
  return (
    <Field 
      placeholder={placeholder} 
      name={name}
      validate={validate}
      component={component} email
      type={type}
      checked={checked}
      />
  )
}