import React, {useState} from 'react';
import style from './Paginator.module.css'

const Paginator = ({totalItemsCount, pageSize, onPageChanged, currentPage, isFetching, portionSize = 10 }) => {

  let pagesCount = Math.ceil (totalItemsCount / pageSize);

  let pages = [];
  for (let i=1; i <= pagesCount; i++){
    pages.push(i);
  }

  let portionCount = Math.ceil(pagesCount / portionSize);
  let [portionNumber, setPortionNumber] = useState(1);
  let leftPortionPagesNumber = (portionNumber - 1) * portionSize + 1;
  let rightPortionPagesNumber = portionNumber * portionSize;
  return(
        <div className={style.paginacia_list}>
          { portionNumber > 1 && 
          <button className={style.button_left} onClick={() => { setPortionNumber(portionNumber - 1); onPageChanged(leftPortionPagesNumber - 10) }}>{'<<'}</button>}

          {pages
            .filter(p => p >= leftPortionPagesNumber && p<=rightPortionPagesNumber)
            .map( (p) => {
            return <>
              {isFetching ? <div className={style.fon_loading}></div> : null}
              <li onClick={()=>{onPageChanged(p)}} className={currentPage === p && style.active_pagination}>{p}</li>
            </>
            })
          }
          { portionCount > portionNumber && 
          <button className={style.button_right} onClick={() => { setPortionNumber(portionNumber + 1);  onPageChanged(leftPortionPagesNumber + 10) }}>{'>>'}</button>}
        </div>
    )
  }

export default Paginator; 