import React from 'react';
import { connect } from 'react-redux';
import { follow, unfollow, setCurrentPage, setFollowingInProgress, getUsersT} from '../../redux/users-reducer';
import Users from './Users';
import Preloader from '../common/preloader/preoader';
import { compose } from 'redux';
import { getUsers, getPageSize, getTotalUsersCount, getCurrentPage, getIsFetching, getFollowingInProgress } from '../../redux/users-selectors';

class UsersContainer extends React.Component {

  componentDidMount() {
    const {currentPage, pageSize} = this.props;
      this.props.getUsersT(currentPage, pageSize)
  }
  onPageChanged = (pageNumber) => {
    const {pageSize} = this.props;
    this.props.getUsersT(pageNumber, pageSize)
  }
  onFollowChanget = (id) => {
    this.props.follow(id)
  }
  onUnFollowChanget = (id) => {
    this.props.unfollow(id)
  }

  render() {
    return <>
    {this.props.isFetching ? <Preloader user={true} /> :null}
    <Users 
      totalUsersCount={this.props.totalUsersCount} 
      onPageChanged={this.onPageChanged} 
      currentPage={this.props.currentPage} 
      pageSize={this.props.pageSize} 
      users={this.props.users} 
      isFetching={this.props.isFetching}
      onFollowChanget={this.onFollowChanget}
      onUnFollowChanget={this.onUnFollowChanget}
      followingInProgress={this.props.followingInProgress}
    />
    </>
  }
}

const mapStateToProps = (state) => {
  return{
    users: getUsers(state),
    pageSize: getPageSize(state),
    totalUsersCount: getTotalUsersCount(state),
    currentPage: getCurrentPage(state),
    isFetching: getIsFetching(state),
    followingInProgress: getFollowingInProgress(state)
  }
}

export default compose (
  connect(mapStateToProps,{follow,
  unfollow, setCurrentPage,
  setFollowingInProgress, getUsersT}),
)(UsersContainer)