import React from 'react';
import style from './users.module.css'
import user_ava from '../../assets/images/notPhotoUsers.png';
import { NavLink } from 'react-router-dom';

const User = ({urs, followingInProgress, onUnFollowChanget, onFollowChanget}) => {
  return (
    <div key={urs.id} className={style.user}>
      <div className={style.ava_box}>
        <div className={style.ava}>
          <NavLink to={'/profile/' + urs.id}>{urs.photos.small ? <img src={urs.photos.small} alt="" /> : <img src={user_ava} alt="" />}
          </NavLink>
        </div>
        <div className={style.subscribe}>
          {urs.followed
            ? <button disabled={followingInProgress.some(id => id === urs.id)} className={style.unfollow_btn} onClick={() => {
              onUnFollowChanget(urs.id)
            }}>Отписаться</button>
            : <button disabled={followingInProgress.some(id => id === urs.id)} onClick={() => {
              onFollowChanget(urs.id)
            }}>Подписаться</button>}

        </div>
      </div>
      {urs.followed
        ? <div className={style.context_box}>
          <div className={style.context_left}>
            <div className={style.name}>{urs.name}</div>
            <div className={style.status}>{urs.status}</div>
          </div>
          <div className={style.context_right}>
            <div className={style.city}>{urs.uniqueUrlName}</div>
            <div className={style.country}>{urs.uniqueUrlName}</div>
          </div>
        </div>
        : <div className={style.context_boxi}>
          <div className={style.context_left}>
            <div className={style.name}>{urs.name}</div>
            <div className={style.status}>{urs.status}</div>
          </div>
          <div className={style.context_right}>
            <div className={style.city}>{urs.uniqueUrlName}</div>
            <div className={style.country}></div>
          </div>
        </div>}
    </div>
  )
}

export default User;